<template>
  <div id="loading">
    <div class="content">
      <div class="image"></div>
      <div class="text">{{text}}...</div>
    </div>
  </div>
</template>
<script>
export default({
  props: {
    text: {
      type: String,
      default: '加载中'
    }
  }
})
</script>
<style lang="scss" scoped>
#loading {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  color: #fff;
  // background: rgba($color: #000000, $alpha: .75);
  background: transparent;
  .content {
    position: absolute;
    left: 50%;
    top: 50%;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background: rgba($color: #000000, $alpha: .75);
    padding: 30px;
    border-radius: 10px;
    width: 200px;
    height: 200px;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    .image {
      width: 100px;
      height: 100px;
      border-width: 4px;
      border-color: #fff #fff #fff transparent;
      border-style: solid;
      border-radius: 50%;
      animation: rotateFn .8s linear infinite;
    }
    @keyframes rotateFn {
      form {
        transform: rotateZ(0deg)
      }
      to {
        transform: rotateZ(360deg)
      }
    }
    .text {
      font-size: 30px;
      line-height: 1.5;
      margin-top: 20px;
    }
  }
}
</style>



