<template>
  <div id="guild" class="maskBox" style="z-index: 100">
    <div class="guild_box">
      <div class="title">
        <b>分享海报示例</b>
      </div>
      <div class="close" @click="closeGuild"></div>
      <div class="tip">长按图片，选择“分享给好友”或“保存到手机”，进行分享</div>
      <div class="image iconSize">
        <img src="../../../assets/img/movie/guild.png" alt="示例">
      </div>
      <div class="button flex-row" @click="closeGuild">好的</div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      
    }
  },
  methods: {
    closeGuild () {
      this.$emit('closeGuild')
    }
  },
  mounted () {

  }
}
</script>
<style lang="scss" scoped>
.maskBox {
  .guild_box {
    width: 560px;
    height: 826px;
    position: relative;
    border-radius: 37.5px;
    color: #333;
    .title {
      font-size: 32px;
      padding: 28px 0 15px;
      text-align: center;
      line-height: 45px;
      border-bottom: 1px solid #eee;
    }
    .tip {
      width: 450px;
      margin: 0 auto;
      padding: 25px 0;
      line-height: 40px;
      font-size: 28px;
      text-align: center;
    }
    .image  {
      width: 433px;
      height: 475px;
      margin: 0 auto;
    }
    .button {
      font-size: 30px;
      color: #FFA800;
      height: 82px;
      border-top: 1px solid #eee;
      bottom: 0;
      position: absolute;
      width: 100%;
    }
    .close {
      width: 60px;
      height: 60px;
      background: url('../../../assets/img/share/close.png') no-repeat center;
      background-size: 23px 22px;
      position: absolute;
      right: 34px;
      top: 21px;
    }
  }
}
</style>



