<template>
  <div class="scrollBox">
    <header-nav :msg="msg" v-if="$utils.showNavBar()" @headerShare="edit"></header-nav>
    <div id='movieImage' :class="{hasHeader: $utils.showNavBar()}">
      <!-- 影评图片 -->
      <div class="movieImage">
        <div class="iconSize image" v-if="img_url">
          <img :src="img_url" alt="海报">
        </div>
      </div>
    </div>
    <!-- 分享类操作 -->
    <div class="bottomShare">
      <!-- APP操作 -->
      <div class="fixedShare" v-if="$config.source_style >= 6">
        <div class="leftEdit">
          <div class="share-box" @click="setShare(-1)">
            <div class="image iconSize">
              <img src="../../../assets/img/my/edit.png" alt="编辑">
            </div>
            <div class="text">{{comment.reviewid ? '编辑' : '写影评'}}</div>
          </div>
        </div>
        <div class="rightShare">
          <div class="share-box" @click="setShare(0)">
            <div class="image iconSize">
              <img src="../../../assets/img/my/share_wechat.png" alt="微信">
            </div>
            <div class="text">微信</div>
          </div>
          <div class="share-box" @click="setShare(1)">
            <div class="image iconSize">
              <img src="../../../assets/img/my/share_friend.png" alt="朋友圈">
            </div>
            <div class="text">朋友圈</div>
          </div>
          <div class="share-box" @click="setShare(2)">
            <div class="image iconSize">
              <img src="../../../assets/img/my/share_qq.png" alt="qq">
            </div>
            <div class="text">QQ</div>
          </div>
          <div class="share-box" @click="setShare(3)">
            <div class="image iconSize">
              <img src="../../../assets/img/my/save.png" alt="保存">
            </div>
            <div class="text">保存本地</div>
          </div>
        </div>
      </div>
      <!-- 微信引导 -->
      <div class="fixedShare isWx" v-else>
        <div class="title">长按上方海报分享给好友或保存到手机</div>
        <div class="button" @click="setShare(6)">查看如何操作</div>
      </div>
      <!-- 普通浏览器 -->
      <!-- <div class="fixedShare normal isWx">
        <div class="title">长按上方海报分享给好友或保存到手机</div>
        <div class="button" @click="setShare(6)">查看如何操作</div>
      </div> -->
    </div>
    <!-- 影评弹框 -->
    <div class="commentEdit" style="z-index: 5" v-if="showEdit">
      <div class="commentEditBox">
        <div class="edit-title">{{details.vod_name}}</div>
        <div class="tip">你对本片打分</div>
        <div class="gradeCount" v-if="comment.score > 0">{{comment.score}}<span class="percennt">分</span></div>
        <div class="scroe" v-if="comment.score > 0">
          <span v-if="comment.score < 2">超烂</span>
          <span v-else-if="comment.score <= 4">较差</span>
          <span v-else-if="comment.score <= 6">一般般</span>
          <span v-else-if="comment.score <= 8">推荐</span>
          <span v-else-if="comment.score <= 10">力荐</span>
        </div>
        <div class="score"></div>
        <div class="grade">
          <div class="grade-icon" ref="parentEl" @click.stop.prevent="setGrade">
            <div class="grade_progress" :style="{width: `${comment.score * 10}%`}"></div>
          </div>
          <!-- <div class="acturally">{{comment.score}}</div> -->
          <div class="comment"></div>
        </div>
        <div class="edit-input">
          <textarea v-model="content" maxlength="50" :placeholder='placeholderText'></textarea>
          <div class="text-right">{{content ? content.length : 0}}/50</div>
        </div>
        <div class="edit-publish" @click="publish" :class="{active: content}">{{comment.reviewid ? '编辑' : '发布'}}</div>
        <div class="close" @click="showEdit = false"></div>
      </div>
    </div>
    <Guild v-if="showGuild" @closeGuild='showGuild = false'></Guild>
    <Loading v-if="showLoading"></Loading>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import Guild from '@/components/movie/common/showguild'
import Loading from '@/components/common/loading'

export default {
  components: {
    HeaderNav,
    Guild,
    Loading
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  data () {
    return {
      msg: '分享给好友',
      img_url: '',
      showEdit: false,
      showGuild: false,
      showLoading: false,
      content: '',
      comment: {
        score: 0
      },
      details: {},
      placeholderText: '写下你的一句经典影评'
    }
  },
  methods: {
    edit () {

    },
    getPoster () {
      let data = {
        vod_id: this.$route.query.vod_id
      }
      this.$api.get('MOVIE_RANDOM', data, res => {
        this.img_url = res.data.img_url + '&timestamp=' + new Date().getTime()
        let img = document.createElement('img')
        img.src = this.img_url
        img.onload = () => {
          this.showLoading = false
        }
      })
    },
    getReview () {
      this.$api.get('MOVIE_REVIEW_LIST', {vod_id: this.$route.query.vod_id}, res => {
        if (res.data.data.length >= 1 && res.data.data[0].is_self === 1) {
          this.comment = res.data.data[0]
          this.content = res.data.data[0].content
        }
      })
    },
    getDetails () {
      this.$api.get('MOVIE_DETAIL', {vod_id: this.$route.query.vod_id}, res => {
        this.details = res.data
      })
    },
    setShare (index) {
      if (index === -1) {
        this.showEdit = true
      } else if (index === 6) {
        this.showGuild = true
      } else if (index === 3) {
        let url = this.img_url
        this.$utils.saveImage(url, () => {
          this.$toast({
            message: '保存成功',
            duration: 1500
          })
        })
      } else {
        let actions = ''
        if (index === 0) {
          actions = 'wechat'
        } else if (index === 1) {
          actions = 'friend'
        } else if (index === 2) {
          actions = 'qq'
        }
        this.setAppShare(actions)
      }
    },
    // 实际分享
    setAppShare (action) {
      let url = this.img_url
      let msg = {
        type: 'image',
        pictures: [url]
      }
      let flag = true
      let id = ''
      if (action === 'qq') {
        flag = true
        id = 'qq'
        // msg.pictures = [`http://sjbz.fd.zol-img.com.cn/t_s480x854c/g5/M00/0F/09/ChMkJlfJQcuIWeyfAAaVOawltHYAAU7hwPMhXYABpVR177.jpg`]
      } else if (action === 'wechat') {
        id = 'weixin'
        flag = true
        msg.extra = {
          scene: 'WXSceneSession'
        }
      } else if (action === 'friend') {
        id = 'weixin'
        msg.extra = {
          scene: 'WXSceneTimeline'
        }
      }
      if (id === 'qq') {
        this.$utils.appDownload(url, fileName => {
          msg.pictures = [fileName]
          this.shareType(id, msg, flag)
        })
      } else {
        this.shareType(id, msg, flag)
      }
    },
    // 分享提取（微信和朋友圈直接分享地址，qq分享图片）
    shareType (id, msg, flag) {
      var w = window.plus.nativeUI.showWaiting('分享中')
      setTimeout(() => {
        this.showShare = false
        w.close()
      }, 5000)
      this.commonState.shares[id].send(msg, (res) => {
        this.showShare = false
        w.close()
        if (flag) {
          this.shareCallback(id)
        } else {
          this.shareCallback(id)
        }
      }, (err) => {
        this.showShare = false
        w.close()
        if (flag) {
          if (err.code === -2) {
            this.$toast({
              message: '分享失败',
              duration: 1500
            })
          } else {
            this.$toast({
              message: '分享失败',
              duration: 1500
            })
          }
        } else {
          this.$toast({
            message: '分享失败',
            duration: 1500
          })
        }
      })
    },
    publish () {
      let data = {
        vod_id: this.$route.query.vod_id,
        score: this.comment.score,
        content: this.content
      }
      if (!this.comment.score || this.comment.score * 1 === 0) {
        return this.$toast({
          message: '请对本片打分',
          duration: 1500
        })
      }
      if (!this.content || this.content.trim() === '') {
        return this.$toast({
          message: '请输入评语',
          duration: 1500
        })
      }
      if (this.comment.reviewid) {
        data.reviewid = this.comment.reviewid
      }
      this.$api.post('MOVIE_REVIEW', data, res => {
        let txt = '发布成功'
        if (data.reviewid) {
          txt = '编辑成功'
        }
        let obj = {
          avatar: this.commonState.userInfo.avatar,
          comment_time: parseInt(new Date().getTime() / 1000),
          content: this.content,
          fabulous: this.comment.fabulous ? this.comment.fabulous : 0,
          is_fabulous: this.comment.is_fabulous ? this.comment.is_fabulous : 0,
          is_self: 1,
          nickname: this.commonState.userInfo.nickname,
          reply: this.comment.reply ? this.comment.reply : 0,
          reviewid: res.data.reviewid,
          score: this.comment.score
        }
        this.comment = obj
        this.$toast({
          message: txt,
          duration: 1500
        })
        setTimeout(() => {
          this.getPoster()
        }, 300)
        this.$nextTick(() => {
          this.showEdit = false
        })
      })
    },
    setGrade (e) {
      let progress = e.clientX - this.$utils.getElLeftPotion(this.$refs.parentEl)
      let width = this.$refs.parentEl.offsetWidth
      let percent = progress / width * 10
      let diff = percent - parseInt(percent)
      if (diff < 0.25) {
        percent = parseInt(percent)
      } else if (percent >= 0.25 && percent.percent > 0.75) {
        percent = parseInt(percent) + 0.5
      } else {
        percent = parseInt(percent) + 1
      }
      if (percent <= 1) {
        percent = 1
      }
      this.comment.score = percent
    }
  },
  watch: {
    'commonState.login_box' (flag) {
      this.wxConfig()
      if (!flag && this.commonState.login_bol) {
        this.getPoster()
        this.getDetails()
        this.getReview()
      }
    }
  },
  mounted () {
    this.getPoster()
    this.getDetails()
    this.getReview()
    this.showLoading = true
  }
}
</script>
<style lang="scss" scoped>
.scrollBox {
  height: 100vh;
  overflow: hidden;
  .movieImage {
    -webkit-overflow-scrolling: touch;
    padding: 40px 30px 200px;
    .iconSize {
      img {
        display: block;
      }
    }
  }
  .bottomShare {
    .fixedShare {
      position: fixed;
      left: 0;
      right: 0;
      min-height: 185px;
      bottom: 0;
      display: flex;
      flex-flow: row nowrap;
      background: rgba($color: #fff, $alpha: .8);
      white-space: nowrap;
      align-items: center;
      padding: 0 30px;
      .leftEdit {
        width: 160px;
        border-right: 1px solid #E5E5E5;
      }
      .rightShare {
        display: flex;
        padding: 0 20px;
        align-items: flex-end;
        padding-right: 0;
        .share-box {
          margin: 0 20px;
        }
      }
      .share-box {
        display: flex;
        justify-content: center;
        flex-flow: column wrap;
        align-items: center;
        .iconSize {
          width: 80px;
          height: 80px;
        }
        .text {
          margin-top: 16px;
          font-size: 28px;
          line-height: 40px;
        }
      }
    }
    .fixedShare.isWx {
      flex-flow: column wrap;
      justify-content: flex-start;
      .title {
        padding: 32px 0 16px;
        line-height: 40px;
        font-weight: 900;
        font-size: 28px;
      }
      .button {
        height: 60px;
        line-height: 45px;
        padding: 9px 0 6px;
        text-align: center;
        background: #F66F52;
        border-radius: 37.5px;
        width: 310px;
        color: #fff;
        font-size: 32px;
        font-weight: 900;
        box-sizing: border-box;
      }
    }
  }
  
  .commentEdit {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba($color: #000000, $alpha: .6);
    .commentEditBox {
      position: absolute;
      top: 160px;
      left: 20px;
      right: 20px;
      background: #fff;
      border-radius: 30px;
      .close {
        width: 60px;
        height: 60px;
        background: url('../../../assets/img/task/close_circle.png') no-repeat center;
        background-size: 46px 46px;
        position: absolute;
        bottom: -100px;
        left: 50%;
        transform: translateX(-50%);
        -webkit-transform: translateX(-50%);
      }
      .gradeCount {
        line-height: 50px;
        text-align: center;
        font-size: 50px;
        color: #f4a82b;
        .percennt {
          font-size: 30px;
        }
      }
      .scroe {
        text-align: center;
        line-height: 50px;
        // color: #f4a82b;
        font-size: 36px;
      }
      .edit-title {
        max-width: 80%;
        text-align: center;
        margin: 0 auto;
        padding: 40px 0 45px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: 900;
        font-size: 36px;
        line-height: 50px;
      }
      .grade {
        height: 70px;
        .grade-icon {
          height: 70px;
          background: url('../../../assets/img/movie/grade.png') no-repeat left top;
          width: 370px;
          background-size: 370px;
          .grade_progress {
            height: 70px;
            background: url('../../../assets/img/movie/grade.png') no-repeat bottom left;
            background-size: 370px;
          }
        }
        .acturally {
          font-size: 36px;
          width: 40px;
        }
      }
      .tip {
        font-size: 32px;
        color: #666;
        margin-bottom: 45px;
        text-align: center;
      }
      .grade {
        justify-content: center;
      }
      .edit-input {
        margin-top: 40px;
        border-bottom: 1px solid #e5e5e5;
        border-top: 1px solid #e5e5e5;
        padding: 30px 30px 16px;
        color: #666;
        height: 350px;
        box-sizing: border-box;
        textarea {
          width: 100%;
          height: calc(100% - 56px);
          display: block;
          font-size: 32px;
          line-height: 1.5;
          border: 0;
        }
        .text-right {
          line-height: 40px
        }
      }
      .edit-publish {
        padding: 42px 0 38px;
        font-size: 36px;
        line-height: 50px;
        box-sizing: border-box;
        text-align: center;
        border-top: 1px solid #f5f5f5;
        &.active {
          color: #F4A82B;
        }
      }
    }
  }
}
#movieImage {
  height: 100vh;
  box-sizing: border-box;
  overflow: auto;
}
</style>



